import Vue from 'vue'
import VueRouter from 'vue-router'

const Login =()=>import (/*webpackChunkName:"login_telelogin_teleregister_checkcode"*/ '../components/user/Login.vue')
const TeleLogin =()=>import (/*webpackChunkName:"login_telelogin_teleregister_checkcode"*/ '../components/user/TeleLogin.vue')
const TeleRegister =()=>import (/*webpackChunkName:"login_telelogin_teleregister_checkcode"*/ '../components/user/TeleRegister.vue')
const CheckCode =()=>import (/*webpackChunkName:"login_telelogin_teleregister_checkcode"*/ '../components/user/CheckCode.vue')

const Home =()=>import (/*webpackChunkName:"home_findindex"*/ '../components/Home.vue')
const FindIndex =()=>import (/*webpackChunkName:"home_findindex"*/ '../components/find/FindIndex.vue')

const SongListIndex =()=>import (/*webpackChunkName:"songlistindex_listdetail"*/ '../components/detailPage/SongListIndex.vue')
const NewSongIndex =()=>import (/*webpackChunkName:"newsongindex_playmusic"*/ '../components/detailPage/NewSongIndex.vue')
const ListDetail=()=>import (/*webpackChunkName:'songlistindex_listdetail'*/ '../components/detailPage/ListDetail.vue')
const PlayMusic=()=>import (/*webpackChunkName:'newsongindex_playmusic'*/ '../components/find/PlayMusic.vue')
const DjIndex =()=>import (/*webpackChunkName:"djindex"*/ '../components/detailPage/DjIndex.vue')

const Mine=()=>import (/*webpackChunkName:'mine'*/ '../components/mine/Mine.vue')

const Clouds=()=>import (/*webpackChunkName:'clouds'*/ '../components/clouds/Clouds.vue')

const Video=()=>import (/*webpackChunkName:'mine'*/ '../components/video/Video.vue')

const Rank=()=>import (/*webpackChunkName:'rank'*/ '../components/detailPage/Rank.vue')


Vue.use(VueRouter)



const router = new VueRouter({
  routes:[
    { path: '/', redirect:'/find',component:Home,children:[
      {path:'/find',component:FindIndex},
      {path:'/mine',component:Mine},
      {path:'/clouds',component:Clouds},
      {path:'/video',component:Video},
    ]},
    { path: '/login', component: Login },
    { path: '/teleregister', component: TeleRegister},
     { path:'/teleregister/checkcode',component:CheckCode},
    { path: '/telelogin', component: TeleLogin },  
    { path: '/songlistIndex' ,component: SongListIndex}, 
    { path: '/newsongIndex',component: NewSongIndex}, 
    { path: '/listdetail' ,component: ListDetail}, 
    { path: '/rank' ,component: Rank}, 
    { path: '/play' ,component: PlayMusic}, 
    { path: '/djIndex' ,component: DjIndex}, 
    { path: '/home', component: Home,redirect:'/find'},
   
    
  ]   
})

export default router


